
define( 'circles/main',['require','Underscore','Backbone','./circleView'],function (require) {

    var _ = require('Underscore');
    var Backbone = require('Backbone');

    var CircleView = require('./circleView');

    var AppView = Backbone.View.extend({
        el: '#app-view',

        initialize: function () {
            console.log('AppView initialized');
        },

        events: {
            "click #btn-load-game": "loadGame"
        },

        loadGame: function () {
            var view = new CircleView();
            this.$('#btn-load-game').addClass('hide');
        }
    });

    return AppView;
});
define('circles', ['circles/main'], function (main) { return main; });
